table {
        width: 100%;
	margin-bottom: 1.3em;
	thead {
		font-weight: bold;

		th {
			text-align: left;
			border-bottom: 4px solid #888;
			font-weight: bold;
			padding: 12px;
			vertical-align: middle;
		}
	}

	tr {
	        border: 1px solid #202020;
		&:nth-child(even) td {
			background: #1e2126;
		}

		td {
			padding: 12px;
		}
	}
}
