.video-thumb-container {
  position: relative;
}

.video-thumb {
  display: block;
  width: 100%;
  height: auto;
}

.video-thumb-icon {
  display: block;
  margin: 0 auto;
  position: absolute;
  width: 100px;
  height: auto;
  top: calc(48% - 30px);
  left: calc(48% - 30px);
  transition: .5s ease;
}

.video-thumb-container:hover .video-thumb-icon {
    fill: #61ffa2 !important;
}
